body {
  background-color: #ffffff;
}

.wrapper {
  display: grid;
  max-width: 1200px;
  margin: 0 auto;

  grid-template-areas:
    "h h"
    "n c";
  grid-template-columns: 2fr 10fr;
  grid-template-rows: 60px 1fr;
  grid-gap: 10px;
}

.wrapper-content {
  grid-area: c;
  background-color: #f2f3f4;
}
